import React, { Component, useEffect, useState } from "react";
import "./Player.scss";
import videoStore from "./VideoStore";

let loadYT;
let youtubePlayerAnchor = "ytPlayer1";

export function YouTube() {
	const [playerConfig, setPlayerConfig] = useState(videoStore.getPlayerConfig());
	const size = useWindowSize();
	let videoWrapper

	useEffect(() => {// on resize window
		playerConfig.height = size.height + 70;
		playerConfig.width = size.width;
		setPlayerConfig(playerConfig);

		if(!videoWrapper) videoWrapper = document.querySelector(".videoWrapper");
		videoWrapper.height = size.height + 70;
		videoWrapper.width = size.width;
	}, [size]);

	useEffect(() => {//on mounted
		const params = new URLSearchParams(window.location.search);
		const videoString = params.get("v") || "dsUQl2Y-rD0.62";
		if (videoString) {
			videoStore.setQueryParams(videoString);
		}

		if (!loadYT) {
			loadYT = new Promise((resolve) => {
				const tag = document.createElement("script");
				tag.src = "https://www.youtube.com/iframe_api";
				const firstScriptTag = document.getElementsByTagName("script")[0];
				firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
				window.onYouTubeIframeAPIReady = () => resolve(window.YT);
			});
		}
		loadYT.then((YT) => {
			console.log("loadYT");
			new YT.Player(youtubePlayerAnchor, playerConfig);
		});

	}, []);

	// Hook
	function useWindowSize() {
		const isClient = typeof window === "object";

		function getSize() {
			return {
				width: isClient ? window.innerWidth : undefined,
				height: isClient ? window.innerHeight : undefined,
			};
		}

		const [windowSize, setWindowSize] = useState(getSize);

		useEffect(() => {
			if (!isClient) {
				return false;
			}

			function handleResize() {
				setWindowSize(getSize());
			}

			window.addEventListener("resize", handleResize);
			return () => window.removeEventListener("resize", handleResize);
		}, []); // Empty array ensures that effect is only run on mount and unmount

		return windowSize;
	}

	return (
		<div
			className='videoWrapper'
			id={youtubePlayerAnchor}
		></div>
	);
}

