import React, { useState } from "react";
import { makeStyles } from "@material-ui/styles";
import { Button, ButtonGroup,  } from "@material-ui/core";
import { StateProvider } from "../lib/react-pan-pinch/store/StateContext";
import { dispatch as dispatchUseBas } from 'use-bus';
import zoom_in from "./images/zoom-in.svg";
import zoom_out from "./images/zoom-out.svg";

export function TransparentPanel() {
	const classes = useStyles();
	const [boxColor, setBoxColor] = useState("red");

	const zoomIn = () => {
		dispatchUseBas({type:'@commands', action:{type:'zoomIn'}})
	};
	const zoomOut = () => {
		dispatchUseBas({type:'@commands', action:{type:'zoomOut'}})
	};
	return (
		<div className={classes.root}>
			<div className={classes.centerContainer}>
				<ButtonGroup size="small" aria-label="small outlined button group">
					<Button
						className={classes.button}
						onClick={zoomIn}
						variant="outlined"
						color="secondary"
					>
						<img src={zoom_in} alt=""/>
					</Button>
					<Button
						className={classes.button}
						onClick={zoomOut}
						variant="outlined"
						color="secondary"
					>
						<img src={zoom_out} alt=""/>
					</Button>
				</ButtonGroup>

			</div>
		</div>
	);
}

const useStyles = makeStyles({
	root: {
		height: "100%",
		textAlign: "center",
		paddingTop: 20,
		paddingLeft: 15,
		paddingRight: 15,
		backgroundColor: "transparent",
	},

	centerContainer: {
		flex: 1,
		height: "90%",
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		flexDirection: "column",
	},

	button: {
		marginTop: 5,
		small: true
	},
});
