export function YoutubeZoom() {
	const classes = useStyles();

	const [type, setType] = useState(true);
	const [limitToBounds, setLimitToBounds] = useState(true);
	const [panningEnabled, setPanningEnabled] = useState(true);
	const [transformEnabled, setTransformEnabled] = useState(true);
	const [pinchEnabled, setPinchEnabled] = useState(true);
	const [limitToWrapper, setLimitToWrapper] = useState(false);
	const [disabled, setDisabled] = useState(false);
	const [dbClickEnabled, setDbClickEnabled] = useState(true);
	const [lockAxisX, setLockAxisX] = useState(false);
	const [lockAxisY, setLockAxisY] = useState(false);
	const [velocityEqualToMove, setVelocityEqualToMove] = useState(true);
	const [enableWheel, setEnableWheel] = useState(true);
	const [enableTouchPadPinch, setEnableTouchPadPinch] = useState(true);
	const [enableVelocity, setEnableVelocity] = useState(true);
	const [limitsOnWheel, setLimitsOnWheel] = useState(false);
	const [transformWrapperFunctions, setTransformWrapperFunctions] = useState({});

	useBus(
		"@commands",
		(params) => {
			try {
				const { type, payload } = params.action;
				switch (type) {
					case "zoomIn":
						transformWrapperFunctions["zoomIn"]({});
						break;
					case "zoomOut":
						transformWrapperFunctions["zoomOut"]({});
						break;
					default:
						console.log("bad type", type);
				}
				/*if (typeof transformWrapperFunctions[type] == "function") {
					transformWrapperFunctions[type]({});
				}*/
			} catch (e) {
				console.log("bad type", type, e.message);
			}
		},
	);

	const setTWFunction = (name, val) => {
		if (name && val) transformWrapperFunctions[name] = val;
		return false;
	};

	return (
		<div className="body">
			<div className="container">
				<div className="row align-items-center">
					<div className="col-lg-12 order-lg-2 example">
						<TransformWrapper
							options={{
								limitToBounds,
								transformEnabled,
								disabled,
								limitToWrapper,
							}}
							pan={{
								disabled: !panningEnabled,
								lockAxisX,
								lockAxisY,
								velocityEqualToMove,
								velocity: enableVelocity,
							}}
							pinch={{ disabled: !pinchEnabled }}
							doubleClick={{ disabled: !dbClickEnabled }}
							wheel={{
								wheelEnabled: enableWheel,
								touchPadEnabled: enableTouchPadPinch,
								limitsOnWheel,
							}}
						>
							{({
								  zoomIn, zoomOut, resetTransform, setDefaultState,
								  positionX, positionY, scale, previousScale,
								  options: { limitToBounds, transformEnabled, disabled }, ...rest
							  }) => (
								<React.Fragment>
									{setTWFunction("zoomIn", zoomIn)}
									{setTWFunction("zoomOut", zoomOut)}
									<div className="element">
										<TransformComponent>
											{/*<Video/>*/}

											<YouTube  />

											{/*<div
												className="video"
												style={{
													position: "relative",
													paddingBottom: "56.25%" ,
													paddingTop: 25,
													height: 0
												}}
											>
												<YouTube/>
											</div>*/}

											{/*<img
												className="zoom"
												src={example_img}
												alt="example-element"
											/>*/}
										</TransformComponent>
									</div>
								</React.Fragment>
							)}
						</TransformWrapper>
					</div>
				</div>
			</div>
		</div>
	);
}

