export default new VideoStore();

export function queryToPlaylist(val) {

  const rate = 1
  const autoPlay = val.a === '1';

  const videos = []
  val.split('~')
      .map(function (item, index) {
        const id = index + 1
        const arr = item.split('.')
        let videoId = arr[0]
        if (!videoId) {
          if (index > 0) {
            videoId = videos[index - 1].videoId
          } else {
            console.error('First videoId is empty')
            throw 'First videoId is empty'
          }
        }
        const startSeconds = arr[1] ? Number(arr[1]) : 0
        const endSeconds = arr[2] ? Number(arr[2]) : null
        if (Number.isInteger(startSeconds) && Number.isInteger(endSeconds) && endSeconds <= startSeconds) {
          console.error('endSeconds must be more startSeconds')
          throw 'endSeconds must be more startSeconds'
        }
        const title = arr[3] ? arr[3] : 'Video ' + id
        videos.push({id, videoId, startSeconds, endSeconds, title})

      });
  // console.log('arrResult', videos)
  return {rate, autoPlay, videos}

}
